(function () {
  'use strict';

  angular
    .module('petstore')
    .directive('psPetSearch', petSearch);

  /** @ngInject */
  function petSearch(petActive) {

    var directive = {
      restrict: 'EA',
      replace: true,
      templateUrl: 'app/components/pet-search/pet-search.html',
      controller: PetSearchController,
      controllerAs: 'vm',
      bindToController: true,
      scope: true
    };

    return directive;

    /** @ngInject */
    function PetSearchController(Pet) {
      var vm = this;
      vm.petId;
      vm.busy = false;
      vm.search = search;

      function search() {
        vm.busy = true;

        Pet.get({'petId': vm.petId}).$promise.then(function (aPet) {
          petActive.pet = aPet;
        })['finally'](function () {
          vm.busy = false;
        });
      }
    }
  }
})();

(function () {
  'use strict';

  angular
    .module('petstore')
    .directive('psPetAdd', petAdd);

  /** @ngInject */
  function petAdd(Pet, alertService) {

    var directive = {
      restrict: 'EA',
      replace: true,
      templateUrl: 'app/components/pet-add/pet-add.html',
      controller: PetAddController,
      controllerAs: 'vm',
      bindToController: true,
      scope: true
    };

    return directive;

    /** @ngInject */
    function PetAddController($scope) {
      var vm = this;

      vm.add = add;

      function reset() {
        $scope.$broadcast('show-errors-reset');
        vm.data = {};
      }

      function add() {
        function tagMap(val, i) {
          return {id: i, name: val};
        };

        var pet = new Pet({
          name: vm.data.name,
          tags: vm.data.tags ? JSON.parse(vm.data.tags).map(tagMap) : [],
          status: vm.data.status ? 'available' : 'outofstock',
          photoUrls: [vm.data.image]
        });

        vm.busy = true;
        pet.$save().then(function (resp) {
          alertService.addAlert({type: 'success', msg: "Well done! You successfully added " + vm.data.name});
          reset();
        })['catch'](function () {
          alertService.addAlert({type: 'danger', msg: "Sorry, the operation is failed. Try again!"});
        }) ['finally'](function () {
          vm.busy = false;
        });
      }
    }
  }
})();

(function () {
  'use strict';

  angular
    .module('petstore')
    .directive('psBusyButton', psBusyButton);

  /** @ngInject */
  function psBusyButton() {
    var directive = {
      restrict: 'A',
      link: link
    };

    return directive;

    function link(scope, element, attrs) {
      var busyAria =
        angular.element('<div role="region" class="sr-only" aria-live="assertive"></div>');
      element.parent().after(busyAria);
      var orig = element.text();

      attrs.$observe('psBusyButton', function (val) {
        if (val === 'true') {
          busyAria.text('Busy working button. Please wait...');
          element.text('Working...');
          element.prop('aria-hidden', 'true');
          element.prop('disable', true);
        }
        else {
          busyAria.text('');
          element.prop('disable', false);
          element.prop('aria-hidden', 'false');
          element.text(orig);
        }
      });
    }
  }

})();

