(function () {
  'use strict';

  angular
    .module('petstore')
    .service('petActive', petActive);

  /** @ngInject */
  function petActive(Pet, alertService) {
    var that = this;
    this.pet;

    this.delete = function (petId) {
      Pet.delete({petId: petId}).$promise.then(function () {
        alertService.addAlert({
          type: 'success',
          msg: "Well done! You successfully deleted " + that.pet.name
        });
        that.pet = null;
      })['catch'](function (error) {
        alertService.addAlert({
          type: 'danger',
          msg: "We're sorry. We were unable to complete your request due to an unexpected error. Please try again."
        });
      });
    }
  }
})();

(function () {
  'use strict';

  angular
    .module('petstore')
    .service('alertService', alertService);

  /** @ngInject */
  function alertService() {
    this.alerts = [];

    this.addAlert = function (alert) {
      this.alerts.push(alert);
    };

    this.closeAlert = function (index) {
      this.alerts.splice(index, 1);
    };
  }
})();

(function () {
  'use strict';

  angular
    .module('petstore')
    .factory('Pet', Pet);

  /** @ngInject */
  function Pet($resource, resourceError) {
    return $resource('/petstore/api/pet/:petId', {petId: '@id'}, {
      save: {
        method: 'POST',
        interceptor: {responseError: resourceError}
      },
      delete: {
        method: 'DELETE',
        interceptor: {responseError: resourceError}
      },
      get: {
        interceptor: {responseError: resourceError}
      }
    });
  }
})();

(function () {
  'use strict';

  angular
    .module('petstore')
    .service('loginModal', loginModal);

  /** @ngInject */
  function loginModal($modal, $rootScope) {
    function assignCurrentUser(user) {
      $rootScope.currentUser = user;
      return user;
    }

    return function () {
      var instance = $modal.open({
        templateUrl: '/app/login/loginModalTemplate.html',
        controller: 'LoginModalCtrl',
        controllerAs: 'vm'
      });

      return instance.result.then(assignCurrentUser);
    };
  }
})();

(function () {
  'use strict';

  angular
    .module('petstore')
    .controller('LoginModalCtrl', LoginModalCtrl);

  /** @ngInject */
  function LoginModalCtrl($scope, $http) {

    this.cancel = $scope.$dismiss;

    this.submit = function (user, password) {
      $http({
        url: '/petstore/api/login',
        method: 'POST',
        data: {
          username:user,
          password:password
        }
      }).then(function (response) {
        $scope.$close(user);
      }, function (error) {
        $log.error(error);
      });
    };
  }
})();

