(function () {
  'use strict';

  angular
    .module('petstore')
    .config(m2e2Timeout)
    .run(runBlock);

  /** @ngInject */
  function runBlock($log, $httpBackend) {

    $log.debug('runBlock end');

    var addPet =
    {};


    $httpBackend.whenPOST('/petstore/api/pet').respond(addPet);

    var getPet = {
      "id": 1,
      "name": "Haireless guinea pig",
      "photoUrls": [
        "assets/pets/1.jpg",
        "assets/pets/2.jpg",
        "assets/pets/3.jpg",
        "assets/pets/4.jpg"
      ],
      "tags": [
        {
          "id": 0,
          "name": "rodents"
        },
        {
          "id": 1,
          "name": "small"
        },
        {
          "id": 2,
          "name": "kids"
        }
      ],
      "status": "available"
    };
    $httpBackend.whenGET('/petstore/api/pet/1').respond(getPet);

    var delPet = {};
    $httpBackend.whenDELETE('/petstore/api/pet/1').respond(delPet);

    $httpBackend.whenGET(/\.html$/).passThrough();

  }

  function m2e2Timeout($httpProvider) {

    $httpProvider.interceptors.push(function ($q, $timeout, $log) {
      return {
        'request': function (config) {
          $log.log('Requesting ' + config.url, config);
          return config;
        },
        'response': function (response) {
          var deferred = $q.defer();

          //Let through views immideately
          if (response.config.url.indexOf(".html") !== -1) return response;

          $timeout(function () {
            deferred.resolve(response);
          }, 2000);

          return deferred.promise;
        }

      }
    });
  }
})();

(function () {
  'use strict';

  angular
    .module('petstore')
    .config(routerConfig);

  /** @ngInject */
  function routerConfig($stateProvider, $urlRouterProvider) {
    $stateProvider
      .state('container', {
        abstract: true,
        templateUrl: 'app/layout/main/main.html',
        controller: 'MainController',
        controllerAs: 'main'
/*        data: {
          requireLogin: true
        }*/
      })
      .state('container.home', {
        url: '/',
        views: {
          'main@container': {
            templateUrl: 'app/states/home/home.html',
            controller: 'HomeController',
            controllerAs: 'home'
          }
        }
      }).state('container.add', {
        url: '/add',
        views: {
          'main@container': {
            templateUrl: 'app/states/add/add.html',
            controller: 'AddController',
            controllerAs: 'add'
          }
        }
      });

    $urlRouterProvider.otherwise('/');
  }

})();

